/* open-sans-regular - latin_cyrillic */
@import 'reset', 'fonts';

html {
  scroll-behavior: smooth;
}
body {
  font-family: "Open Sanss";
  font-size: 16px;
  width: 100%;
  height: 100vh;
  line-height: 30px;
  
}

.mcolor {
  color: #333333;
}

.scolor {
  color: #666666;
}

.title {
  font-size: 35px;
  color: #333333;
}

.container {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 15px;
}

:focus {
  outline: none;
}

a.anchor {
  display: block;
  position: relative;
  top: -99px;
  visibility: hidden;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}

.blue {
  background-color: #3498db;
  color: #fff;
  a {
    color: #fff;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
    }
  }
}

.italic {
  font-style: italic;
}

.header {
  background-color: #ffffff;
  z-index: 1500;
  height: 100px;
  width: 100%;
  box-shadow: 0px 5px 5px 0px rgba(223, 223, 223, 0.6);
  position: fixed;
  &__logo {
    flex: 0 0 80px;
    height: 100%;
    display: flex;
    align-items: center;
    & img {
      max-width: 100%;
    }
  }
  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: 0;
    ul{
      display: flex;
      justify-content: center;
      align-items: center;
      li {
        display: flex;
        justify-content: space-between;
        margin: 0 9px;
        &:last-child {
          margin-right: 0;
        }
      }
      
      a {
        display: flex;
        align-items: center;
        padding: 0 10px;
        height: 40px;
        font-size: 14px;
        text-decoration: none;
        color: #666666;
        &:hover {
          background-color: #eeefef;
          color: #2f7bb8;
        }
      }

      .zayava {
        background-color: #2f7bb8;
        color: #fff;
        border-radius: 20px;
        width: 139px;
        justify-content: center;
      }
    }
  }  
}

.header__burgbody {
  display: none;
}



.registration {
  margin-top: 100px;
  background-color: #f7f7f7;
  width: 100%;
  padding: 0;
  height: 665px;
  .container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  .star {
    color: red;
  }
  
  &_info {
    line-height: 30px;
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-right: 25px;
    margin-top: 100px;
    .title {
      line-height: 50px;
      text-align: center;
    }
    p {
      padding: 0 23px;
      font-size: 18px;
      text-align: center;
      padding-top: 30px;
      color: #787878;
    }
  }
  &_form {
    width: 50%;
    height: 100%;
    padding-left: 25px;
    h2 {
      font-size: 22px;
      margin-top: 50px;
    }
    .form__item {
      .input {
        width: 100%;
        height: 52px;
        border: 1px solid #bababa;
        border-radius: 5px;
        margin-top: 5px;
        padding: 0 15px;
        &:focus{
          outline: none;
          border: 1px solid #66afe9;
          box-shadow: 0px 0px 10px 1px rgba(102, 175, 233, 0.5);        
        }
      }
    }
    .scales {
      margin-top: 23px;
    }
    #wisited {
      text-decoration: none;
      &:visited {
        color:blue;
      }
      &:hover {
        text-decoration: underline;
      }
    }
    .form__body {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      height: 100%;
      padding-bottom: 30px;
    }
    #send {
      width: 65%;
      height: 50px;
      align-self: center;
      background-color: #337AB7;
      border: none;
      border-radius: 23px;
      font-size: 12px;
      color: #fff;
      text-transform: uppercase;
    }
  }
  
}

.courses {
  background-color: #fff;
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 562px;
    padding: 70px 0;
    .title {
      margin-bottom: 40px;
    }
    .slick-slider {
      width: 100%;
      min-width: 0;
    }
    .slick-list {
      width: 100%;
      overflow: hidden;
      min-width: 0;
      margin: 0 auto;
    }
    .slick-track {
      min-width: 0;
      display: flex;
      width: 100%;
      margin: 0 auto;
    }
    .slick-slide.slick-active {
      width: 350px;
    }
    .slick-dots{
      display: flex;
      justify-content: center;
      align-items: center;
    } 
    .slick-dots li{
      list-style: none;
      margin: 0 10px;
    } 
    .slick-dots li.slick-active button{
      background-color: #666666;
    } 
    .slick-dots li button{
      font-size: 0;
      border: none;
      background-color: #b3b3b3;
      width: 12px;
      height: 12px;
      border-radius: 50%;
    }
    .post {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      padding: 0 35px;
      img {
        margin-bottom: 20px;
      }
      &__title {
        color: #666666;
        font-size: 22px;
        border-bottom: 1px solid #3498db;
        padding-bottom: 18px;
        margin-bottom: 17px;
        width: 100%;
        flex-wrap: nowrap;
      }
      &__body {
        color: #888888;
        text-align: center;
        font-size: 16px;
        width: 100%;
      }
    }
  }
}



.achieve {
  height: 310px;
  .container{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  &__item {
    text-align: center;
    width: 25%;
  }
  &__number {
    font-size: 26px;
  }
}

.reviews {
  background-color: #fff;
  text-align: center;
  display: flex;
  justify-content: center;
  min-height: 525px;
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
  &__title {
    margin-bottom: 14px;
    margin-top: 76px;
  }
  &__text {
    font-size: 22px;
  }
  &__person {
    margin-top: 40px;
  }
  .brackets {
    display: flex;
    max-width: 976px;
    margin: 0 auto;
    &__left {
      display: flex;
      flex-direction: column;
      justify-content: start;
    }
    &__right {
      display: flex;
      flex-direction: column;
      justify-content: end;
    }
  }
  .slick-slider {
    height: max-content;
    display: flex;
    flex-direction: column;
    min-width: 0;
    width: 100%;
    overflow: hidden;
    margin-bottom: 20px;
  }
  .slick-track {
    display: flex;
    overflow: hidden;
  }
  .slick-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1220px;
  }
  .slick-dots{
    margin-top: 70px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  .slick-dots li{
    list-style: none;
    margin: 0 10px;
  } 
  .slick-dots li.slick-active button{
    background-color: #666666;
  } 
  .slick-dots li button{
    font-size: 0;
    border: none;
    background-color: #b3b3b3;
    width: 12px;
    height: 12px;
    border-radius: 50%;
  }
}

.videos {
  background-color: #f7f7f7;
  .container {
    padding: 65px 15px;
    text-align: center;
    iframe {
      max-width: 1020px; 
      height: 30vw;
      margin-top: 30px;
      width: 100%;
    }
  }
  .title {
    margin-bottom: 30px;
  }
  
}

.mentors {
  background-color: #fff;
  &__slider {
    min-width: 0;
  }
  .title {
    text-align: center;
    margin-top: 70px;
    margin-bottom: 30px;
  }
  &__item {
    display: flex;
    min-width: 0;
    img {
    margin-right: 100px;
    width: 320px;
    height: 340px;
    }
  }
  &__title {
    font-size: 21px;
    padding-bottom: 20px;
    border-bottom: 1px solid #f7f7f7;
    color: #3498db;
  }
  &__text {
    font-size: 18px;
    color: #666666;
    p {
      margin-top: 20px;
    }
  }
  .slick-slider{
    overflow: hidden;
    width: 100%;
  } 
  .slick-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .slick-track{
    display: flex;
  }
  .slick-dots{
    margin-top: 20px;
    margin-bottom: 60px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  .slick-dots li{
    list-style: none;
    margin: 0 10px;
  } 
  .slick-dots li.slick-active button{
    background-color: #666666;
  } 
  .slick-dots li button{
    font-size: 0;
    border: none;
    background-color: #b3b3b3;
    width: 12px;
    height: 12px;
    border-radius: 50%;
  }
  
}

.contacts {
  .container {
    padding: 40px 0;
    display: flex;
    justify-content: space-around;
  }
  &__item {
    text-align: center;
    width: 25%;
  }
}


.footer {
  margin-top: auto;
  display: flex;
  height: 156px;
  background-color: #f7f7f7;
  .container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    p {
      font-size: 16px;
      color: #3199dc;
      padding: 10px;
      a {
        text-decoration: none;
        color: #8f8f8f;
        &:hover{
          text-decoration: underline;
        }
      }
    }
  }
}

